import React, { useState, useEffect } from 'react';
import articles from './article-content';
import NotFoundPage from './NotFoundPage';
import CommentsList from './CommentsList';
import UpvotesSection from './UpvotesSection';
import CommentSection from './CommentSection';

const ArticlePage = ({match}) => {

    const name = match.params.name;
    const article = articles.find(article=> article.name === name);
    console.log(article);

    const [articleInfo, setArticleInfo]= useState({ upvotes:0, comment: []});
    console.log(articleInfo.upvotes);
    useEffect(()=> {
        const fetchData = async ()=> {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            console.log(body);
            setArticleInfo(body);
        }
        fetchData();
    }, [name]);

    if (!article) return <NotFoundPage />

    return(
        <>
        <h1>{article.title}</h1>
        <UpvotesSection articleName= {name} setArticleInfo = {setArticleInfo} upvotes = {articleInfo.upvotes}  />
        {article.content.map((paragraph, key)=> <p key={key}>{paragraph}</p>)}
        <CommentsList comments = {articleInfo.comment} />
        <CommentSection articleName= {name} setArticleInfo = {setArticleInfo} />
        </>
    );
    
};

export default ArticlePage;
